@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+Condensed:wght@300;400;500;700&family=IBM+Plex+Sans:wght@100;300;400;700&display=swap);
body {
  margin: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  font-family: "IBM Plex Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: black;

  /* Credits to @pawel_czerwinski - unsplash */
  background-image: url(/static/media/background.4622c418.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  color: #ffffff;
}

* {
  margin: 0;
  padding: 0;
  cursor: none;
  pointer-events: none;
  -webkit-user-select: none;
}

a {
  pointer-events: initial;
}

.carousel {
  pointer-events: initial;
}

.gradient-top {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 200px;
  z-index: -1;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.gradient-bot {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 200px;
  z-index: -1;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.header {
  padding: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header .logo {
  font-family: "IBM Plex Sans Condensed";
  font-size: 18px;
  font-weight: 400;
  color: #ffffff;
}

.header .logo span {
  font-weight: 500;
}

.header .social {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 160px;
  height: 20px;
}

.social a {
  color: #ffffff;
  text-decoration: none;
  opacity: 0.6;
}

.social a:hover {
  opacity: 1;
}

.sidebar {
  position: absolute;
  top: 50%;
  left: 60px;
  transform-origin: top left;
  transform: rotate(-90deg) translate(-50%, -50%);
}

.sidebar a {
  color: #fff;
  text-decoration: none;
  font-size: 16px;
  font-weight: 100;
  margin: 10px;
  opacity: 0.8;
}

.sidebar .active {
  font-weight: 500;
  opacity: 1;
}

.sidebar a:hover {
  opacity: 1;
}

.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 60px;
  display: flex;
  justify-content: space-between;

  font-size: 16px;
  font-family: "IBM Plex Sans Condensed";
  font-weight: 100;
  opacity: 0.8;
}

.cursor-inner, .cursor-outer {
    position: fixed;
    top: 0;
    left: 0;
    border-radius: 100%;
    transform: translate(-50%, -50%);
    pointer-events: none;

}

.cursor-inner {
    width: 8px;
    height: 8px;
    background-color: #ffffff;
    z-index: 1000;
}

.cursor-outer {
    width: 24px;
    height: 24px;
    background-color: rgba(255, 95, 56, 0.5);
    z-index: 999;
    transition: all 0.1s ease-out;
}
.about {
  display: flex;
  justify-content: center;
  width: 80vw;
  max-height: 80vh;
  font-size: 16px;
  position: absolute;
  font-family: "IBM Plex Sans Condensed";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.4);
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
  overflow: auto;
  pointer-events: initial;
}


.about h4 {
  font-size: 18px;
  padding-bottom: 20px;
}

.about > div {
  width: 50%;
}

.about .info {
  padding: 30px;
}

.about a {
  text-decoration: none;
  color: #ffffff;
  font-weight: 600;
}

.about ul {
  list-style-type: none;
}

.about p,
.about ul {
  line-height: 1.4em;
}

.carousel {
  position: absolute;
  left: 17%;
  right: 5%;
}

.carousel.idle {
  transition: all 0.2s ease-in;
}

.idle .carousel-image {
  transition: transform 0.2s ease-in;
}

.idle .carousel-image p {
  transition: all 0.1s;
}


.carousel-center {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  height: 50vh;
  top: 50%;
  transform: translateY(-50%);
}

/* Hover effects */
.carousel-center.idle:hover .carousel-image p {
  /* opacity: 0.8; when detail page done */
  opacity: 0;
}
.carousel-center.idle:hover {
  transform: translateY(-50%) scale(1.08);
}
.carousel-center.idle:hover .carousel-image {
  transform: scale(1.1) translateX(-8%);
}

.carousel-center.idle:hover .carousel-image img {
  -webkit-filter: brightness(0.7);
          filter: brightness(0.7);
}

.carousel-center.idle:hover ~ .carousel-bot {
  transform: translateY(100%) ;
}
.carousel-center.idle:hover ~ .carousel-top {
  transform: translateY(-100%) ;
}
