/* Carousel text styling */
.carousel-info {
  display: none;
  margin: 20px 0;
  font-size: 18px;
  z-index: 10;
}
.carousel-info p {
  margin-bottom: 20px;
}

.text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  width: 40%;
  height: 100%;
}

.title {
  position: relative;
  font-family: "IBM Plex Sans";
  font-weight: bold;
  width: 60vw;
}
.title h1 {
  position: relative;
  line-height: 100%;
  font-size: 6vw;
  color: transparent;
}

.title .outline {
  position: absolute;
  top: 0;
  left: 0;
  color: transparent;
  -webkit-text-stroke: 1px white;
  z-index: 5;
}

/* Duotone carousel image styling */
.carousel-image {
  display: none;
  position: relative;
  height: 100%;
  width: 100%;
}

.carousel-image img {
  position: absolute;
  height: 100%;
  z-index: 1;
  width: 100%;
  object-fit: cover;
  -webkit-filter: grayscale(1) brightness(0.4);
          filter: grayscale(1) brightness(0.4);
}

.carousel-image p {
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 8;
  margin: 20px;
  font-weight: bold;
  -webkit-text-stroke: 0.5px white;
  color: transparent;
  font-size: 28px;
  opacity: 0;
}

/* specific styling dependant on whether carousel is center/bot/top */
.carousel-center .carousel-image,
.carousel-center .carousel-info {
  display: inline;
  display: initial;
}

.carousel-center .title h1 {
  color: #ffffff;
}

.carousel-top {
  top: 0;
  transform: translateY(-30%);
  z-index: -1;
}

.carousel-bot {
  bottom: 0;
  transform: translateY(30%);
  z-index: -1;
}

.carousel-top .title, .carousel-bot .title {
  opacity: 0.25;
  width: 70vw;
}

.carousel.clicked .text {
  display: none;
}
.carousel.clicked {
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  transform: none;
  background-color: rgba(0,0,0,.5);
}
.carousel.clicked img {
  width: auto;
  max-width: 90%;
  object-fit: contain;
  -webkit-filter: none;
          filter: none;
  left: 50%;
  transform: translateX(-50%);
}

